import { TextField, Button } from "@mui/material";
import React, { useState } from "react";

import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import { isInsideGeofence } from "./data.service";

interface SearchFormProps {
  searchData: (searchTerm: string) => void;
}

const SearchForm: React.FC<SearchFormProps> = ({ searchData }) => {
  const [searchTerm, setSearchTerm] = useState("");
  const [open, setOpen] = React.useState(false);

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
    searchData(searchTerm);
  };

  const checkGeoFence = () => {
    if (searchTerm) {
      navigator.permissions
        .query({ name: "geolocation" })
        .then(function (result) {
          // Will return ['granted', 'prompt', 'denied']
          if (result.state === "granted") {
            navigator.geolocation.getCurrentPosition(
              successCallback,
              errorCallback
            );
          }
          if (result.state === "denied") {
            alert(
              ` please grant permission to access your device's location. Without it, the search feature will be unable to retrieve relevant details.`
            );
          }
          console.log(result.state);
        });
    }
  };

  const successCallback = (position: GeolocationPosition) => {
    const latitude = position.coords.latitude;
    const longitude = position.coords.longitude;
    if (isInsideGeofence(latitude, longitude)) {
      console.log("Inside geofence.");
      searchData(searchTerm);
    } else {
      console.log("Outside geofence.");
      setOpen(true);
    }
  };

  const errorCallback = (error: GeolocationPositionError) => {
    console.error("Error accessing geolocation:", error.message);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <form>
        <TextField
          value={searchTerm}
          required
          style={{ width: "60%", marginLeft: "10px" }}
          fullWidth
          label="Search by name..."
          id="fullWidth"
          onChange={(e) => setSearchTerm(e.target.value)}
        />
        <Button
          onClick={checkGeoFence}
          disabled={!searchTerm}
          type="button"
          variant="outlined"
          color="primary"
          style={{
            marginLeft: "10px",
            width: "30%",
            height: "54px"
          }}
        >
          Search
        </Button>
      </form>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"NIKOO HOMES-II Owners's Search"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Accessing the restricted data outside the premises is not permitted.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Close</Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default SearchForm;
